@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff"), url("../fonts/Montserrat-SemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
  border: none;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol,
li {
  list-style: none;
}

li {
  position: relative;
}

img {
  vertical-align: top;
  width: 100%;
  height: 100%;
}

button {
  cursor: pointer;
  background-color: unset;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

:focus {
  outline: none;
}

/* ------------------------general-------------------- */
body {
  font-family: "Montserrat", "Arial", "Helvetica", sans-serif;
  min-width: 320px;
  font-weight: 400;
  font-style: normal;
  overflow-x: hidden;
  padding-top: 120px;
  padding-bottom: 120px;
}

body.lock,
html.lock {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
}

.wrapper {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

.title {
  color: #254085;
  font-weight: 700;
}
.title--basic {
  font-size: 22px;
  text-align: center;
}
.title span {
  color: #FE5339;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  width: 100%;
  max-width: 294px;
  min-height: 62px;
  background-color: #FFDD2D;
  border-radius: 10px;
  color: #222222;
  font-weight: 600;
  font-size: 18px;
}

/* ------------------------blocks-------------------- */
.first-screen--bottom-indent {
  margin-bottom: 22px;
}
.first-screen__title {
  margin-bottom: 16px;
  font-size: 22px;
  line-height: 1.36;
}
.first-screen__title--basic {
  font-size: 22px;
}
.first-screen__subtitle {
  margin-bottom: 32px;
  color: #0F348C;
  font-size: 12px;
  line-height: 1.5;
}
.first-screen__img {
  position: relative;
  display: block;
  max-height: 340px;
  height: 100%;
  width: 105%;
  margin-left: -7px;
  margin-bottom: 50px;
}
.first-screen__img img {
  display: block;
  height: 100%;
  width: 100%;
  max-height: inherit;
  object-fit: contain;
  object-position: center top;
}
.first-screen__button {
  font-size: 16px;
}

.triggers {
  padding-top: 82px;
  background-image: url("../img/svg/pixels-1.svg");
  background-repeat: no-repeat;
  background-position: center top;
}
.triggers__title {
  margin-bottom: 64px;
}
.triggers__list {
  margin-bottom: 64px;
}
.triggers__item:nth-child(n+2) {
  margin-top: 48px;
}
.triggers__description {
  margin-bottom: 27px;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.39;
  text-align: center;
  color: #428ECC;
}
.trigger__icon {
  max-height: 40px;
  max-width: 40px;
  margin-bottom: 11.6px;
}
.trigger__icon svg {
  width: 100%;
  height: 100%;
  fill: #428ECC;
}
.trigger__title {
  margin-bottom: 10px;
  color: #428ECC;
  font-size: 20px;
  line-height: 1.3;
  font-weight: 600;
}
.trigger__description {
  color: #242424;
  line-height: 1.56;
}